import Vue from 'vue'
import Vuex from 'vuex'
import uuid from 'uuid'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
		notes : {
			'root' : {
				id : 'root',
				children: [],
				parents: []
			}
		},

		focusedElement : null,
		
		clipboard : {
			note : null,
			instance: null,
			event : null
		},

		CONSTANTS : {
			SPELLCHECK : 'false',
			TAB_WIDTH: 4,
		}


	},

	getters: {
		getText: (state) => (id) => {
			return state.notes[id].text;
		},
		
		getNotes(state){
			return state.notes;
		},

		getFocusedElement(state){
			return state.focusedElement;
		},

		getFocusedInstance(state){
			return state.focusedElement.instance;
		},

		getFocusedNote(state){
			var note_id = state.focusedElement.focusedInstance.note_id;
			var focused_note = state.notes[note_id];
			return focused_note;
		},

		getNoteById: (state) => (id)=>state.notes[id]
		,

		getNewNote(state){
			var note_id =  uuid();
			var newNote = {
				id: note_id,
				text: '',
				children: []
			};
			state.notes = {...state.notes, [newNote.id]:newNote};
			return newNote;
		},

		noteHasChild: (state) => (note,child) => {
			return note.children.indexOf(child.id) != -1
		}
	},

	mutations: {

		registerNewNote(state, id){
			var newNote = {
				id: id,
				text: '',
				children: [],
				parents: []
			};
		
			state.notes = {...state.notes, [id]:newNote};

		},

		addChildToParent(state, {child, parent}){
			parent.children.push(child.id);
			child.parents.push(parent.id);
			
		},

		removeChildFromParent(state,{child, parent}){ 
				parent.children = parent.children.filter(candidate => candidate!=child.id);
				child.parents = child.parents.filter(candidate => candidate!=parent.id);
		},


		setFocusedElement(state,element){
			if (state.focusedElement != element)
			state.focusedElement = element;
		},

		setText(state,{id, text}){
			state.notes[id].text = text;
		},

		loadNotes(state,notes){
			state.notes = notes;
		},

		focusElement(state,element){
			state.focusedElement = element; 
		},

		//Focuses the same tag as is currently focused of a different instance
		focusInstance(state,instance){
			var tag = state.focusedElement.tagName.toLowerCase();
			state.focusedElement = instance.elements[tag];
		},

		copyToClipboard(state,{instance, event}){
			if (instance.note.id == 'root'){return;}
			state.clipboard.note = instance.note;
			state.clipboard.instance = instance;
			state.clipboard.event = event;
		},

		clearClipboard(state){
			state.clipboard.instance = null;
			state.clipboard.event = null;
		},

		swapPosition(state, {parent,note1,note2}){
			var i1 = parent.children.indexOf(note1.id);
			var i2 = parent.children.indexOf(note2.id);
			if (i1 == -1 || i2 == -1){return;}
			parent.children[i1] = note2.id; 
			parent.children[i2] = note1.id;
		}

	},

	actions: {
	
	}
})
