<template>
	<div :class="{NoteContainer:true, selected:isSelected}" @click.stop="onClick" style="padding: 2px">
		<NoteInstance class="instance" v-for="child in children" :note_id="child"/>
	</div>
</template>

<script>
import {mapState, mapGetters} from 'vuex';
import NoteInstance from './NoteInstance.vue'

const uuidv1 = require('uuid/v1');
export default {

	name: 'NoteContainer',
	
	components: {
		NoteInstance
	},

	data(){
		
		return {
			id: 'root',
			note_id: 'root',
			_focusedInstance: null, // instanced is focused iff it is currently a textarea.
			_selectedInstances : [this],
			root: this,
			is_root: true,
			clipboard : {
				notes : []

			}
		}
	},

	methods: {

		callFn(fn,forAllSelectedInstances=true,isDoableOnRoot=false){
			fn = fn.bind(this);
			var instances = forAllSelectedInstances ? this.selectedInstances.filter(()=>(true)) : [this.selectedInstances[0]];
			var index = 0;
			var cycle = function(){
				if (index >= instances.length){ return; }
				var instance = instances[index];
				if (isDoableOnRoot || !instance.is_root){fn(instance)}
				index += 1;
				this.$nextTick(cycle);
			}.bind(this);
			cycle();
		},

		registerNewChild(){
			var new_note_id = uuidv1();
			this.$store.commit('registerNewNote', new_note_id);
			this.$store.commit('addChildToParent', [new_note_id, this.note_id]);
		},


		removeNoteFromParent(){
			var child = this.note; 
			var parent = this.parentNote;
			this.$store.commit('removeChildFromParent',{child, parent});
		},

		selectThis(){
			this.selectInstance(this);
		},	

		selectInstance(instance){
			if(this.selectedInstances.includes(instance)){return;}
	
			this.selectedInstances = [...this.selectedInstances, instance]; 
		
		},

		deselectInstance(instance){
			this.selectedInstances = this.selectedInstances.filter( (i) => (i!=instance) );
		},

		instanceIsSelected(instance){ 	
			return this.selectedInstances.includes(instance)
		},

		onClick(e){
			this.$emit('click',{instance: this, event: e});
		},

		getNewNote(){
			var newNoteId = uuidv1();
			this.$store.commit('registerNewNote',newNoteId);
			var newNote = this.$store.getters.getNoteById(newNoteId); 
			return newNote;
		},
		

		//next instance below this on screen. Wraps around.
		nextVisibleInstance(instance){
			if (instance.is_root){return this.$children.length>0 ? this.$children[0] : this}
			if (!instance.isVisible()){return this.nextVisibleInstance(instance.$parent)}
			if (instance.isExpanded && instance.$children.length>0){return instance.$children[0]}

			return this.nextSiblingDown(instance);
		},

		//goes up to closest ancestor that has a next sibling and returns that. Next sibling of last top-level instance is root.	
		nextSiblingDown(instance){
			if (instance.is_root){return instance.$children.length>0 ? instance.$children[0] : instance}
			if (!instance.isLastSibling()){return instance.nextSibling()}
			return (instance.$parent.is_root ? instance.root : this.nextSiblingDown(instance.$parent))		
		},

	
		previousVisibleInstance(instance){
			if (instance.is_root){return this.$children.length>0 ? this.$children[this.$children.length-1] : this}
			if (!instance.isVisible()){return instance.$parent.isVisible() ? instance.$parent : this.previousVisibleInstance(instance.$parent)}

			var i = instance.previousSibling(); 
			var next = this.nextVisibleInstance(i);
			while (next != instance){
				i = next; 
				next = this.nextVisibleInstance(next);
			}
			return i;

		},

		deleteInstance(instance){
			if(!instance.wasDeleted()){
				this.selectInstance(this.previousVisibleInstance(instance),true);
			}
				this.deselectInstance(instance);
				this.$store.commit('removeChildFromParent',{child: instance.note, parent: instance.$parent.note});
		},

		addToClipboard(instance){
			this.clipboard.notes.push(instance.note);
		},

		attachAllNotesOnClipboard(instance){
			this.clipboard.notes.forEach(function(note){
				this.$store.commit('addChildToParent',{child: note, parent:instance.note});
				instance.expand();
			}.bind(this));
		},

		

		//Many functions that act on instances also act on the container. These fns are simple counterparts of noteinstance fns. 
		path(){return [this]},
		expand(){}, 
		isVisible(){return true},
		wasDeleted(){return false},
		toggleChildren(){},
		scrollIntoView(){}



	},
 
	computed: {
		...mapState({
			children(state){
				var children = state.notes[this.note_id].children;
				return children;
			},

			note(state){
				return state.notes[this.note_id];
			}
		}),

		isSelected(){ return this.instanceIsSelected(this)},

		firstSelectedInstance(){ return this.selectedInstances[0] },

		selectedInstances : {
			set(instances){
				this.focusedInstance = null;
				if (instances.length>0){instances[0].scrollIntoView();}
				this.$data._selectedInstances = instances;

				
			},
			get(){
				return this.$data._selectedInstances;
			}
		},

		focusedInstance : {
			set(instance){
				if (this.$data._focusedInstance) {this.$data._focusedInstance.textArea.blur();}
				this.$data._focusedInstance = instance;

				if (this.$data._focusedInstance){
					this.selectInstance(instance);
					this.$nextTick(function(){
						var ta = this.$data._focusedInstance.textArea;
						ta.focus();
						ta.selectionEnd = ta.selectionStart;
					}.bind(this));	
				}
				
			},
			get(){
				return this.$data._focusedInstance;
			}
		},

	},


	created(){
		document.addEventListener('keydown', e => {
			if (this.selectedInstances.length == 0){return;}
			if (this.focusedInstance != null && (e.key!='Alt' && e.key!='Tab' && e.key!='Insert')){return;}
			switch(e.key){
				case 'Alt':
					e.preventDefault();
					this.focusedInstance = null;
					break;

				case 'Insert':
					//Design deicision: clicking insert with multiple instances selected creates ONE new note and adds it to all selected.
					
					var newNote = this.getNewNote();
					var newSelectedInstances = [];


					this.callFn(function(parentInstance){
						this.$store.commit('addChildToParent',{child: newNote, parent: parentInstance.note});
						parentInstance.expand();
						this.$nextTick(function(){
							newSelectedInstances.push(parentInstance.$children[parentInstance.$children.length-1]);
						})
					},true,true);

					if (!e.altKey){
						this.selectedInstances = newSelectedInstances;
					}

					setTimeout(function(){
						if(this.selectedInstances[0]!=this){
							this.selectedInstances[0].scrollIntoView()
						}}.bind(this),50)
					break;

				case 'Delete': 
					this.callFn(this.deleteInstance);
					break; 

				//Copy selected instances to local clipboard and clear OS clipboard
				case 'c':
					if (e.ctrlKey){
						this.clipboard.notes = [];
						this.callFn(this.addToClipboard);
					}

					//Clear the OS clipboard
					navigator.permissions.query({name: "clipboard-write"}).then(result => {
					  if (result.state == "granted" || result.state == "prompt") {
					  	navigator.clipboard.writeText("");
					  }
					});

					break;

				//If ctrl-v is pressed and the OS clipboard is empty, then copy notes of local clipboard under selected instances
				case 'v': 
					if (e.ctrlKey){
						navigator.clipboard.readText().then(text => {
							if (text!=""){ return }
							else { this.callFn(this.attachAllNotesOnClipboard,true,true) }

						});
					}
					break;

				case 'Enter':
					if (this.selectedInstances.length == 1){
						var inst = this.selectedInstances[0];
						if (inst != this){
							e.preventDefault();	
							this.focusedInstance = inst;
						}
					}
					break;

				case 'ArrowUp':
					e.preventDefault();
				
					if (!e.shiftKey){
						var si = this.selectedInstances[0];
						var pvi = this.previousVisibleInstance(si);
						this.selectedInstances = [pvi];
					}
					if (e.shiftKey){
						//Swap the first selected instance and the one above it (if it is not first child)
						var si = this.selectedInstances[0];
						var ps = si.previousSibling();
						if (si != ps){this.$store.commit("swapPosition",{parent:si.parentNote, note1:si.note, note2:ps.note})}
						si.$parent.$forceUpdate();
						this.selectedInstances = [ps];
					}
					break;

				case 'ArrowDown':
					e.preventDefault();

					if (!e.shiftKey) {
						var si = this.selectedInstances[0];
						var nvi = this.nextVisibleInstance(si);
						this.selectedInstances = [nvi];	
					}

					if (e.shiftKey){
						var si = this.selectedInstances[0];
						var ns = si.nextSibling();
						if (si != ns){this.$store.commit("swapPosition",{parent:si.parentNote, note1:si.note, note2:ns.note})}
						si.$parent.$forceUpdate();
						this.selectedInstances = [ns];
					}
					break;

				case 'Tab':
					e.preventDefault();
					if (this.focusedInstance){
						//Tabbing logic. Assumes nothing is highlighted.
						
						var ta = this.focusedInstance.textArea;
						var text = ta.value;
						var index = ta.selectionStart;
						var tab_width = this.$store.state.CONSTANTS.TAB_WIDTH;
						var spaces_to_add = tab_width - index%tab_width;
						ta.value = text.substring(0,index) + " ".repeat(spaces_to_add) + text.substring(index);
						ta.selectionStart = ta.selectionEnd = index + spaces_to_add;
					}

					else{
						this.selectedInstances.forEach(function(inst){inst.toggleChildren();});
					}
						
					break;	
			}
		});	
	},

	mounted(){
		this.$on('click',function({instance, event}){
			if (event.ctrlKey){
				this.selectInstance(instance);
			}
			else { 
				if (instance != this.focusedInstance){
					this.selectedInstances = [instance];	
				}
			}

		});

		this.$on('dblclick',function({instance, event}){
			this.focusedInstance = instance;
		})
	}
};

</script>

<style scoped>
	
	.NoteContainer {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100%; 
		background-color: grey;
		margin: 3px;
		overflow: auto;
	}

	.instance {
		display: block;
		width: 100%;
		padding: 1px;
	}

	.selected{ 
	    background-color: #8C8888;
	    box-shadow: 0 0 10px red;
	}


</style>