<template>
<div>
	<div id="actionsBar">
		<div id="download_button" class="button cell"  v-on:click="download">
			Download
		</div>
		<div id="upload_button" class="button cell" v-on:click="clickInput">  
			Upload 
		</div>
		<input id="in" type="file" class="cell" style="display: none">
	</div>
</div>
</template>

<script>

export default{
	name: "ActionsBar",
	components: {
	},

	data(){
		return {
			inputElement : null //populated in "mounted"
		}
	},
	
	methods: {
		download(){
			var notes = this.$store.getters.getNotes;
			var jsonNotes = JSON.stringify(notes);

			var fileBlob = new Blob([jsonNotes], {type: "application/octet-binary"});
			var link = document.createElement("a");
			link.setAttribute("href", URL.createObjectURL(fileBlob));
			link.setAttribute("download", "notes.json");
			link.click();
		},

		upload(){
			var store = this.$store;
			var tFile =  this.inputElement.files[0];
			tFile.text().then(res => store.commit('loadNotes',JSON.parse(res)));
		},

		clickInput(){this.inputElement.click()}

	},

	created(){
		document.addEventListener('keydown', e => {
			switch(e.key){
				case 's':
					if (e.ctrlKey){
						e.preventDefault();
						this.download();
					}
					break;

				case 'o':
					if (e.ctrlKey){
						e.preventDefault();
						document.getElementById("in").click();
					}
					break;
			}
		});
	},

	mounted(){
		this.inputElement = document.getElementById("in");
		this.inputElement.addEventListener('input' ,e=>{
			this.upload();
		});
	}
}
	
</script>

<style scoped>
	#actionsBar {
		display: table;
		position: relative;
		width: 100%;
		height: 50px;
		border: 2px solid lightblue;
		background-color: red;
		z-index: 1;
	
	}


	.button {
		background-color: DodgerBlue;
		width: auto;
		cursor: pointer;
		margin-top: 10px;
		line-height: 100%;
		width: 10%;
		border: 3px solid green;

	}

	.cell {
		display: table-cell;
		vertical-align: middle;
		text-align: center;
		width: 20%;
		font-size: 20px;
	}

	#in {
		display: block;
		position: absolute;
		
		
	}

</style>