<template>
	<div class="base">
		<div :class="{elements:true, selected: isSelected, focused: isFocused}" @click.stop="onClick" @dblclick.stop="onDblclick" v-bind:style="{height:height+'px'}">
			<button @click="toggleChildren" :class="{boldButton : children.length>0}"> {{button_text}} </button>
			<textarea type="text" :spellcheck="SPELLCHECK" :readonly="!isFocused" v-model:value="text"/> </textarea>
		</div>

		<div v-if="children_instantiated" :class="{hidden : button_text=='>' }" >
			<NoteInstance v-for="child in children" :note_id="child"/>
		</div> 

	</div>
</template>

<script>
import {mapState, mapGetters} from 'vuex'

const uuidv1 = require('uuid/v1');
export default {

	name: 'NoteInstance',
	
	components: {
	},

	props:{
		note_id: String
	},

	data(){
		
		return {
			button_text: '>',

			children_instantiated : false,

			is_root : false,

			height : 10
		}
	},

	methods: {

		onClick(e){
			this.$emit('click',{instance: this, event: e});
		},

		onDblclick(e){
			this.$emit('dblclick',{instance: this, event: e});
		},

		toggleChildren(){
			this.children_instantiated = true; 
			this.button_text = (this.button_text == '>' ? 'v' : '>');

		},

		
		isVisible(){
			if (this.$parent.$children.length == 0){return false;}
			if (this.$parent == this.root){return true;}
			return (this.$parent.isVisible() && this.$parent.button_text == 'v')
		},

		//Calling on first sibling returns last one
		previousSibling(){
			if(this.index==0){return this.siblings[this.siblings.length-1]}	
			return this.siblings[this.index-1]
		},

		//Calling on last sibling returns first one.
		nextSibling()	 {return this.siblings[(this.index+1)%this.siblings.length]},

		expand(){
			if(this.isFolded){this.toggleChildren()}
		},

		isLastSibling(){return this.index == this.siblings.length-1},

		path(){
			return this.$parent.path().concat([this]);
		},

		indexAsSibling(){return this.$parent.$children.indexOf(this)},

		wasDeleted(){ return this.indexAsSibling() == -1},

		resize(){
			if(this.height<this.textArea.scrollHeight){this.height = this.textArea.scrollHeight};
			
			//When deleting a line, scrollHeight is much higher than it should be. This is a workaround.
			if(this.height>this.textArea.scrollHeight){
				this.height = 0;
				this.$nextTick(function(){
					this.height = this.textArea.scrollHeight;
				}.bind(this));
			};
		},

		scrollIntoView(){ 
				var bounding = this.textArea.getBoundingClientRect();
				if (
					bounding.top < 0 ||
					bounding.bottom > window.innerHeight
				) 
				{this.textArea.scrollIntoView();}
		}

	},
 
	computed: {
		...mapState({
			children(state){
				return state.notes[this.note_id].children;
			},

			parents(state){
				return state.notes[this.note_id].parents;
			},

			parentNote(state){
				return this.$parent.note;
			},

			note(state){
				return state.notes[this.note_id];
			}
		}),

		show_children(){return this.children_instantiated && this.button_text=='v'},

		primaryDiv(){return this.$el.children[0]},

		textArea(){return this.$el.children[0].children[1]},

		siblings(){return this.$parent.$children},

		index(){return this.indexAsSibling()},

		isFolded(){
			return this.button_text == '>';
		},

		focusedElement: {
			set(element){
				this.root.focusedElement = element;
			},
			get(){
				return this.root.focusedElement;
			}
		},
		
		text: {
			set(text){
				this.$store.commit('setText', {id: this.note_id, text});
				if(this.height != this.textArea.scrollHeight){this.resize()};
			},
			get(){
				this.$nextTick(this.resize.bind(this));
				return this.$store.getters.getText(this.note_id);

			}
		},

		root(){
			if (this.is_root){return this;}
			return this.$parent.root;
		},

		isSelected(){
			var root = this.root; 
			var isSel = root.instanceIsSelected(this);
			
			return isSel;
		},

		isFocused(){
			return this.root.focusedInstance == this;
		},

		isExpanded() { return this.button_text == 'v'},

		SPELLCHECK() { return this.$store.state.CONSTANTS.SPELLCHECK;}
	},

	mounted(){

		this.$on('click',function({instance, event}){
			this.$parent.$emit('click',{instance, event});
		});

		this.$on('dblclick',function({instance, event}){
			this.$parent.$emit('dblclick',{instance, event});
		});
	}
};

</script>
<style scoped>

	textarea {
		height: auto;
		overflow-y: auto;
		width: 100%;
		padding: 0;
		overflow-y: hidden;
		padding-left:3px;
		padding-top: 1px;
		padding-bottom: 3px; /*larger than top padding to account for 'g'*/

		font-family: sans-serif;

		-webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
	    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
	    box-sizing: border-box;    

	}

	button,textarea {
		border: 1px solid black;
	}

	button {
		border-right: none;
	}

	.boldButton {
		font-weight: bold;
	}

	.base {
		float: right;
		width: 98%;
		display: block;
		padding-top: 2px;
	}	

	.elements{
		min-height: 1em;
		width: 100%;
		display: flex;	
	}

	.selected > textarea{ 
	    border-color: red;
	    border-width: 2px;
	}

	.hidden{
		display: none;
	}

</style>